import uniGrid from "@/components/uni-grid/uni-grid.vue"
import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue"
export default {
	
	data() {
		return {
			items:[]
		};
	},
	onShow:function(){
		this.fetch()
	},
	onLoad:function(){
		this.fetch()
	},
	methods:{
		async switchID(userCode,isUsing){
			if (isUsing ==0){
				let data ={
					userCode: userCode
				}
				const res = await this.$http.get('/user/cut_account',{data: data})
				uni.setStorageSync('token',res.data.data.msg)
				uni.showToast({
					title: '切換成功'
				});
				this.fetch()
			}	
		},
		async fetch(){
			const res = await this.$http.get('user/account_tree')
			this.items = res.data.data.list
			console.log(res)
		},
		toAddSub:function(){
			this.items.forEach((item)=>{
				if (item.isUsing==1){
					if (item.isMain ==1){
						uni.navigateTo({
							url: './addSub'
						});
					}else {
						uni.showToast({
							title: '子賬戶不能創建子賬戶',
							icon:'none',
							duration: 1000
						});
					}
				}
			})
			
		},
		async submit(){
			uni.showLoading({
				title: '歸集中',
				mask: true
			})
			const res = await this.$http.get('/user/gather')
			uni.hideLoading()
			uni.showToast({
				title: '歸集成功'
			});
		}
	}
}
